import { useAssistantEffect } from './useAssistantEffect';

/**
 * `useWidgetSideEffect()` is a hook that allows you to listen for side effects emitted by widgets on client side.
 * It is used to listen for side effects that are not related to the assistant hub tools or skills.
 */
export function useWidgetSideEffect<T>(
  handler: (sideEffectPayload: T, sideEffectName: string) => void,
  sideEffectName?: string | string[],
) {
  useAssistantEffect(handler, undefined, sideEffectName);
}
